@font-face {
  font-family: 'inter-medium';
  src: url("/fonts/Intermedium.woff2") format("woff2"), url("/fonts/Intermedium.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'inter-regular';
  src: url("/fonts/Interregular.woff2") format("woff2"), url("/fonts/Interregular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'inter-semibold';
  src: url("/fonts/Intersemibold.woff2") format("woff2"), url("/fonts/Intersemibold.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: 'playfairD';
  src: url("/fonts/PlayfairDisplay-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'roboto_condensedregular', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.left {
  padding-top: 50px;
  max-width: 272px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .left .btn {
    margin-top: 50px; }

.logo {
  font-family: "inter-semibold", sans-serif;
  font-size: 22px;
  display: flex;
  align-items: center; }

.logo__block {
  width: 48px;
  height: 48px;
  padding: 7px;
  margin-right: 16px;
  background: #5E81F4;
  border-radius: 12px;
  box-sizing: border-box;
  text-align: center;
  font-size: 24px;
  line-height: 1.3;
  font-family: "playfairD", serif;
  color: #fff; }

.logo__text {
  color: #5E81F4; }

.btn {
  padding-top: 17px;
  padding-bottom: 17px;
  background: #5E81F4;
  border: none;
  border-radius: 10px;
  box-sizing: border-box;
  cursor: pointer;
  font-family: "inter-semibold", sans-serif;
  font-size: 16px;
  line-height: 1.1;
  color: #fff; }

.btn_b {
  padding-right: 30px;
  padding-left: 30px; }

.nav {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center; }

.nav_m {
  margin-top: 50px; }

.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
  margin-left: 50%; }

.nav__item {
  display: flex;
  margin-bottom: 45px; }
  .nav__item:last-child {
    margin-bottom: 0; }

.nav__link {
  font-family: "inter-medium", sans-serif;
  font-size: 16px;
  line-height: 1;
  text-decoration: none;
  color: #8DA1B5;
  border-right: 3px solid transparent;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  padding: 5px 0;
  transition: all .3s ease;
  margin-left: auto;
  width: 100%; }
  .nav__link:hover {
    color: #272829;
    border-right: 3px solid #5E81F4; }
    .nav__link:hover .nav__icon {
      fill: #272829; }

.nav__icon {
  margin-right: 25px;
  fill: #8DA1B5;
  transition: all .3s ease; }

.dashboard {
  width: 20px;
  height: 20px; }

.all {
  width: 20px;
  height: 20px; }

.resources {
  width: 22px;
  height: 18px; }

.friends {
  width: 22px;
  height: 20px; }

.chat {
  width: 20px;
  height: 17.5px; }

.settings {
  width: 22px;
  height: 22px; }

.active-nav {
  border-right: 3px solid #5E81F4; }

.upgrade {
  background: #F5F5FB;
  border-radius: 12px;
  padding: 90px 21px 25px;
  text-align: center;
  position: relative; }

.upgrade__icon {
  width: 85px;
  height: 99px;
  fill: #5E81F4;
  position: absolute;
  top: -20%;
  left: 50%;
  transform: translate(-50%); }

.upgrade__text {
  font-size: 16px;
  font-family: 'inter-medium', sans-serif;
  line-height: 1.4; }

.upgrade .btn {
  width: 100%; }

.upgrade_m {
  margin: 110px 40px 0; }

.page {
  display: flex; }
